<template>
    <div class="login">
        <van-nav-bar >
            <template v-slot:left>
                <div class="logo">
                    <img class="auto-img" src="../assets/images/home_active.png" >
                </div>
                <div class="text">Luckin Coffee</div>
            </template>
            <template #right>
                <div class="right-title" @click="enterHome">
                    先逛一逛
                </div>
            </template>
        </van-nav-bar>
        <div class="content">
            <div class="login-title-box">
                <div class="back">欢迎回来!</div>
                <div class="login-text">Please login to your accounts</div>
            </div>
            <div class="form-box">
                <van-field v-model="userLoginInfo.phone" label="手机号" placeholder="请输入手机号" />
                <van-field v-model="userLoginInfo.password" :type="isPasswordShow?'text':'password'" label="密码" placeholder="请输入密码" right-icon="closed-eye" @click-right-icon="toggleType" />
                <div class="forget"><span>忘记密码？</span></div>
            </div>
            <div class="login-btn">
                <van-button  block color="#0C34BA" round @click="login">登 录</van-button>
                <van-button  block round class="register-btn" @click="Pop">注 册</van-button>
            </div>
        </div>
        <van-popup v-model="isPop" position="bottom" closeable >
            <div class="register-box">
                <div class="register-title">注册</div>
                <div class="form-box">
                    <van-field v-model="userRegisterInfo.phone" label="手机号" placeholder="请输入手机号(11位)" />
                    <van-field v-model="userRegisterInfo.password" :type="isRegisterPasswordShow?'text':'password'" label="密码" placeholder="请输入密码(6-16位)" right-icon="closed-eye" @click-right-icon="toggleRegisterType" />
                    <van-field v-model="userRegisterInfo.nickName" type="text" label="昵称" placeholder="昵称(1-10位)" />
                    <van-button  block round class="register-btn" color="#0c34ba" @click="register">注 册</van-button>
                </div>
            </div>
        </van-popup>
    </div>
</template>

<script>
    import '../assets/less/login.less'
    import {utils} from '../assets/js/utils'
    import {mapMutations} from "vuex"
    export default {
        name:'Login',
        data(){
            return {
                //登录信息
                userLoginInfo:{
                    phone:'',
                    password:'',
                },
                //注册信息
                userRegisterInfo:{
                    phone:'',
                    password:'',
                    nickName:''
                },
                //是否弹出
                isPop:false,
                //是否显示密码
                isPasswordShow:false,
                //注册时，是否显示密码
                isRegisterPasswordShow:false,
            }
        },
        created(){
            this.changeShopbagCount(0);
            this.changeIsLoadShopbagCount(false);
        },
        methods:{
            ...mapMutations(["changeShopbagCount","changeIsLoadShopbagCount"]),
            //弹出
            Pop(){
                this.isPop = true;
            },
            //关闭弹出
            closePop(){
                this.isPop = false;
            },
            //切换密码显示与隐藏
            toggleType(){
                if(this.isPasswordShow){
                    this.isPasswordShow = false;
                    return ;
                }
                this.isPasswordShow = true;
            },
            //注册时切换密码显示与隐藏
            toggleRegisterType(){
                if(this.isRegisterPasswordShow){
                    this.isRegisterPasswordShow = false;
                    return ;
                }
                this.isRegisterPasswordShow = true;
            },
            //注册验证
            register(){
                let userRegMsg = {
                    phone:{
                        value:this.userRegisterInfo.phone,
                        reg:/^1[3-9]\d{9}$/,
                        errorMsg:"手机号格式错误"
                    },
                    password:{
                        value:this.userRegisterInfo.password,
                        reg:/^[A-Za-z]\w{5,15}$/,
                        errorMsg:"密码支持字母数字_组合且首字符必须是字母"
                    },
                    nickName:{
                        value:this.userRegisterInfo.nickName,
                        reg:/^[\u4e00-\u9fa5A-Za-z0-9]{1,10}$/,
                        errorMsg:"昵称支持汉字字母数字组合"
                    }
                }
                //表单验证
                if(!utils.validForm(userRegMsg)){
                    return false;
                }
                //发起注册请求
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/register',
                    data:{
                        appkey:this.appkey,
                        nickName:this.userRegisterInfo.nickName,
                        password:this.userRegisterInfo.password,
                        phone:this.userRegisterInfo.phone
                    }
                }).then(res => {
                    if(res.data.code == 100) {
                        this.isPasswordShow = false;
                        //关闭弹出层
                        this.isPop = false;
                        //关闭加载提示
                        this.$toast.clear();
                        //显示成功信息
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                    }else {
                        //关闭加载提示
                        this.$toast.clear();
                        //显示错误信息
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                    }
                }).catch(err => {
                    //请求发生错误
                    console.log("请求失败",err);
                })
            },
            //登录
            login(){
                let userLoginMsg = {
                    phone:{
                        value:this.userLoginInfo.phone,
                        reg:/^1[3-9]\d{9}$/,
                        errorMsg:"手机号格式错误"
                    },
                    password:{
                        value:this.userLoginInfo.password,
                        reg:/^[A-Za-z]\w{5,15}$/,
                        errorMsg:"密码支持字母数字_组合且首字符必须是字母"
                    },
                }
                //表单验证
                if(!utils.validForm(userLoginMsg)){
                    return false;
                }
                //加载提示
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration:0
                });
                //发送登录请求
                this.axios({
                    method:'POST',
                    url:this.baseUrl + '/login',
                    data:{
                        appkey:this.appkey,
                        password:this.userLoginInfo.password,
                        phone:this.userLoginInfo.phone
                    }
                }).then(res => {
                    //请求成功
                    //关闭加载提示
                    this.$toast.clear();
                    if(res.data.code == 200){
                        //登录成功
                        //将token保存到cookie
                        this.$cookies.set('tokenString',res.data.token,'1d');
                        //显示登录成功
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                        //跳转到其他页面
                        this.$router.push({name:'Home'});

                    }else {
                        //显示登录失败
                        this.$toast({
                            message:res.data.msg,
                            forbidClick:true,
                            duration:1000
                        });
                    }
                }).catch(err => {
                    this.$toast({
                        message:err,
                        forbidClick:true,
                        duration:1000
                    })
                })
            },
            //进入首页
            enterHome(){
                this.$router.push({name:"Home"});
            }   
        }
    }
</script>
